:root {
    --flickr-pink: #f72585ff;
    --byzantine: #b5179eff;
    --purple: #7209b7ff;
    --purple-2: #560badff;
    --trypan-blue: #480ca8ff;
    --trypan-blue-2: #3a0ca3ff;
    --persian-blue: #3f37c9ff;
    --ultramarine-blue: #4361eeff;
    --dodger-blue: #4895efff;
    --vivid-sky-blue: #4cc9f0ff;
}

* {
    margin: 0 ;
    padding: 0;
}

body {
    font-family: 'Ubuntu', sans-serif;
    background-color: #4cc9f0ff;
    background-color: var(--vivid-sky-blue);
}

a {
    text-decoration: none;
}

.header {
    max-height: 200px;
    overflow: hidden;
}

.content {
    min-height: calc(100vh - 255px);
}

.footer {
    display: flex;
    justify-content: center;
    height: 55px;
    background-color: #f72585ff;
    background-color: var(--flickr-pink);
}

.active {
    color: yellow;
}

.unactive {
    color: white;
}
